@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400&display=swap");

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Raleway", sans-serif;
  background-color: #007cc2;
}

nav {
  padding: 30px 10px 0 10px;
  background-color: #007cc2;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.scoreboard {
  margin-right: 10%;
  width: 300px;
  padding: 10px;
  border-radius: 10px;
  color: #e53136;
  display: flex;
  justify-content: space-between;
  background-color: #ffd425;
}

.scoreboard span {
  font-size: 20px;
  font-weight: 500;
}

.logo {
  display: flex;
  align-items: center;
  margin-left: 10%;
  color: #ffd425;
}

.logo h1 {
  font-weight: 300;
}

.logo img {
  height: 72px;
}

.item-wrapper {
  width: 80%;
  margin: 60px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.item {
  width: 180px;
  margin: 20px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  padding: 10px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  transition: 0.3s all ease-in-out;
  border: 2px solid #ffd425;
}

.item:hover {
  transform: translateY(-10px);
}

.item img {
  width: 100%;
  height: 190px;
}

.item span {
  color: white;
  margin: 10px;
}
